	export default {
		data() {
			return {
				form:{
					messageTitle:''	,//标题
					messageDetails:	'' //	内容

				},
				
			};
		},
		onNavigationBarButtonTap:function(val){
			 
			this.tapSettingButton() 
		    
		},
		onLoad:function(options){

		},
		methods:{
